html {

	height: 100%;
}

body {
	font-size: 16px;
	font-weight: 400;
	height: 100%;
}

header,
main,
footer {
	padding-left: 128px;

	@include max(lg) {
		padding-left: 0;
	}
}

a {
	text-decoration: none;
	color: $textMainColor;
}

ul,
ol,
nav {
	list-style: none;
	padding: 0;
}

.main{
	width: 100%;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
}